ks-lightbox  {
    display: block;
}

ks-lightbox .content  {
    display: flex;
    flex-flow: column nowrap;

    padding: 10px;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    user-select: none;
}

ks-lightbox .close {
    position: absolute;
    top: 15px;
    right: 15px;

    backdrop-filter: var(--lightbox-element-filter);
    background-color: var(--lightbox-element-color);
    border-radius: 100%;
    padding: 5px;

    cursor: pointer;
    user-select: none;

    z-index: 1000000;
    transition: var(--transition-background-color),
                transform 0.1s ease;
}

ks-lightbox .close:hover {
    background-color: var(--lightbox-element-color-hover);
    transform: scale(1.05, 1.05);
}

ks-lightbox .close:active {
    background-color: var(--lightbox-element-color-active);
    transform: scale(0.95, 0.95);
}

ks-lightbox .lightbox-full {
    width: 100%;
    flex: 1;
}

ks-lightbox .lightbox-thumb {
    margin-top: 10px;
    width: 100%;
}

ks-lightbox .swiper-slide {
    touch-action: pan-y;
}

ks-lightbox .lightbox-thumb .swiper-slide {
    height: 70px;
    width: 70px;
    overflow: hidden;

    opacity: 0.6;
    transition: var(--transition-opacity);
}

ks-lightbox .lightbox-thumb .swiper-slide:hover {
    opacity: 0.8;
}

ks-lightbox .lightbox-thumb .swiper-slide-thumb-active {
    opacity: 1 !important;
}

ks-lightbox .swiper-pagination {
    display: none;
}

ks-lightbox .swiper-pagination  {
    bottom: 0px !important;
    padding: 5px;

    backdrop-filter: var(--lightbox-element-filter);
    background-color: var(--lightbox-element-color)
}

ks-lightbox .swiper-pagination-bullet  {
    background-color: #ffffff;
}

ks-lightbox .swiper-pagination-bullet-active  {
    background-color: #ffffff;
}


@media only screen and (max-width: 720px) {
    ks-lightbox .content {
        padding: 0;
    }
    
    ks-lightbox .swiper-pagination {
        display: block;
    }

    ks-lightbox .lightbox-thumb {
        display: none !important;
    }

    ks-lightbox .lightbox-full .swiper-slide ks-img img {
        border-radius: 0px;
    }
}