import { Component, h, Prop } from '@stencil/core';
import { product } from "../../../global/data/product";

@Component({
	tag: 'ks-product-title',
	styleUrl: 'product-title.css'
})
export class ProductTitle {
	@Prop() brandLink: boolean = false;

	render() {
		const name = product.get("name");
		const brand = product.get("brand");

		let title = <h1>{name}</h1>;
		
		if(this.brandLink && brand){
			const brandLength = brand.name.length;
			const brandLink = brand.link;
			const index = this.brandIndex();

			if(index !== -1) {
				const prefix = name.substr(0, index);
				const brand = name.substr(index, brandLength);
				const suffix = name.substr(index + brandLength);
				
				title = <h1>{prefix}<a href={brandLink}>{brand}</a>{suffix}</h1>;
			}
		}

		if(product.get("breadcrumbs")) return [
			<div class="breadcrumbs">
				{product.get("breadcrumbs").map(item => 
					<a href={item.link}>{item.name} </a>
				)}
			</div>,
			title
		];

		return title;
	}

	brandIndex() {
		return product.get("name").toLowerCase().indexOf(
			product.get("brand").name.toLowerCase()
		);
	}
}