import { Component, h, Method, Element, Prop } from '@stencil/core';
import { LightboxImageData } from './lightbox-data';
import Swiper, {Pagination, Thumbs} from 'swiper';

Swiper.use([Pagination, Thumbs]);

@Component({
	tag: 'ks-lightbox',
	styleUrl: 'lightbox.css'
})
export class Lightbox {
    @Element() root: HTMLElement;

    @Prop() data: LightboxImageData[];

    overlay: HTMLKsOverlayElement;
    thumbs: Swiper;
    carousel: Swiper;

    componentDidRender() {
        this.overlay = this.root.querySelector("ks-overlay");
    }

    @Method() async show(index: number = 0) {
        this.overlay.show().then(()=>{
            if(this.carousel == undefined) {
                this.thumbs = new Swiper('.lightbox-thumb', {
                    observer: true,
                    observeParents: true,
                    
                    grabCursor: true,
                    slidesPerView: "auto",
                    centerInsufficientSlides: true,
                    watchSlidesProgress: true,
                    spaceBetween: 10,
                    resistanceRatio: 0.7
                });

                this.carousel = new Swiper('.lightbox-full', {
                    observer: true,
                    observeParents: true,

                    centeredSlides: true,
                    slidesPerView: "auto",
                    spaceBetween: 20,
                    preventInteractionOnTransition: true,
                    thumbs: {
                        swiper: this.thumbs
                    },
                    pagination: {
                        el: '.swiper-pagination',
                    },
                });
            }
            
            
            this.carousel?.slideTo(index, 0, false);
        });
    }

    @Method() async hide() {
        this.overlay.hide();
    }

	render() {
		return <ks-overlay dark>
            <div class="content">
                <div class="swiper-container lightbox-full">
                    <div class="swiper-wrapper">
                        {this.data.map(image => 
                            <div class="swiper-slide">
                                <ks-img contained center target=".lightbox-full" src={image.full.url}
                                        width={image.full.width} height={image.full.height}>
                                </ks-img>
                            </div>
                        )}
                    </div>
                    <div class="swiper-pagination"></div>
                </div>

                <div class="swiper-container lightbox-thumb">
                    <div class="swiper-wrapper">
                        {this.data.map(image => 
                            <div class="swiper-slide">
                                <ks-img contained center src={image.thumb.url}
                                        width={image.thumb.width} height={image.thumb.height}
                                ></ks-img>
                            </div>
                        )}
                    </div>
                </div>
            </div>

            <div class="close" onClick={()=>this.overlay.hide()}>
                <ks-icon name="x" size={1.2}></ks-icon>
            </div>
        </ks-overlay>;
	}
}