import { Component, h, Element, Prop, Build } from '@stencil/core';
import { product } from "../../../global/data/product";
import Swiper, {Thumbs} from 'swiper';

Swiper.use([Thumbs]);

@Component({
    tag: 'ks-product-images',
    styleUrl: 'product-images.css'
})
export class ProductImages {
    @Element() root: HTMLElement;
    @Prop() delay: number = 0;

    @Prop({mutable: true, reflect: true}) loaded: boolean = false;

    carousel: Swiper;
    thumbs: Swiper;

    rendered: boolean = false;
    componentDidLoad() {
        if(this.rendered) return;
        this.rendered = true;
        
        if(Build.isBrowser)
            this.initialize();
    }

    initialize() {
        const thumbs_enabled = product.get("images").length > 1;
        if(thumbs_enabled) {
            this.thumbs = new Swiper('.thumb', {
                observer: true,
                observeParents: true,
                    
                grabCursor: true,
                slidesPerView: "auto",
                preventInteractionOnTransition: true,
                centerInsufficientSlides: true,
                watchSlidesProgress: true,
                spaceBetween: 3,
                on: {
                    init: () => {
                        this.loaded = true;
                    },
                },
            });
        }

        this.carousel = new Swiper('.preview', {
            observer: true,
            observeParents: true,

            spaceBetween: 30,
            grabCursor: true,
            autoHeight: true,
            thumbs: thumbs_enabled ? {
                swiper: this.thumbs
            } : undefined
        });
    }

    showLightbox(index: number) {
        this.root.querySelector("ks-lightbox").show(index)
    }

	render() {
		return[
            <div class="swiper-container preview swiper-container-initialized swiper-container-horizontal swiper-container-pointer-events swiper-container-autoheight">
                <div class="swiper-wrapper">
                    {product.get("images").map((image, index) => 
                        <div class={[
                            "swiper-slide",
                            index == 0 ? "swiper-slide-active" : null,
                            index ==  1 ? "swiper-slide-next" : null
                        ].join(" ")}>
                            <ks-img2 fill sync={index == 0}
                                    src={image.preview.url}
                                    webp={image.preview.webp}
                                    width={image.preview.width}
                                    height={image.preview.height}
                                    onClick={()=>this.showLightbox(index)}
                            ></ks-img2>
                        </div>
                    )}
                </div>
            </div>,
            product.get("images").length > 1 ?
                <div class="swiper-container thumb swiper-container-horizontal swiper-container-pointer-events swiper-container-thumbs">
                    <ks-loader dark></ks-loader>

                    <div class="swiper-wrapper">
                        {product.get("images").map((image, index) => 
                            <div class={[
                                "swiper-slide swiper-slide-visible",
                                index == 0 ? "swiper-slide-active swiper-slide-thumb-active" : null,
                                index ==  1 ? "swiper-slide-next" : null
                            ].join(" ")}>
                                <ks-img2 sync={index < 6}
                                    src={image.thumb.url}
                                    webp={image.thumb.webp}
                                    width={image.thumb.width} height={image.thumb.height}
                                ></ks-img2>
                            </div>
                        )}
                    </div>
                </div>
            : null,
            <ks-lightbox data={product.get("images")}>
            </ks-lightbox>
		];
	}
}
